/* index.css */

/* Apply a basic reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Set a basic font and background color for the whole app */
  body {
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
    color: #333;
  }
  
  /* Style the root element */
  #root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  /* Add some padding and center content */
  .container {
    margin: 0 auto;
    padding: 20px;
    max-width: 1200px;
    width: 100%;
  }
  
  /* Basic styling for headers */
  h1, h2, h3, h4, h5, h6 {
    margin-bottom: 0.5em;
  }
  
  /* Basic styling for paragraphs */
  p {
    margin-bottom: 1em;
  }
  
  /* Add some styling for buttons */
  button {
    font-size: 1em;
    padding: 0.5em 1em;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  /* Add some basic link styles */
  a {
    color: #007bff;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  